import { gameList as gameListApi } from '@/api/game';
let gameList = uni.getStorageSync('gameList') || {};

const state = {
	data: gameList
},
getters = {
	data(state) {
		return state.data;
	}
},
mutations = {
	
}
, actions = {
	list({ commit }, param) {
		gameListApi().then(res=>{
			const { data } = res
			uni.setStorageSync('gameList',data)
		})
	}
};

export default {
    namespaced: true,
	state,
	getters,
	mutations,
    actions
}
