import request from './../util/request';

export function statistics(id) {
	return request({
		url: '/account/statistics/id/' + id,
		method: 'GET',
	})
}

export function releaseList(id, data) {
	return request({
		url: '/account/releaseList',
		method: 'GET',
		data
	})
}


export function releaseInit(id, data) {
	return request({
		url: '/account/releaseInit/id/' + (id?id:0),
		method: 'GET',
		data
	})
}

export function releaseSubmit(data) {
	return request({
		url: '/account/release',
		method: 'POST',
		data
	})
}

export function changePassword(data) {
	return request({
		url: '/account/changePassword',
		method: 'POST',
		data
	})
}
